import React from 'react';

import { Card, Button } from 'react-bootstrap';
import { Line } from "react-chartjs-2";

import DataSource from './DataSource';

class ContentCard extends DataSource {
  getValues() {
    var indices = [];
    var values = [];
    for (var i = 0; i < 10; i++) {
      indices.push(i.toString());
      values.push((Math.random() * 50).toFixed(2));
    }

    return [indices, values];
  }

  getData() {
    var values = this.getValues();
    if (values[0].length === 0) {
      return null;
    }

    const data = {
      labels: values[0],
      datasets: [
        {
          label: '',
          data: values[1],
          fill: false,
          backgroundColor: '#2279B5',
          borderColor: '#2279B5',
          borderWidth: 2,
          pointRadius: 0,
          hitRadius: 20,
          cubicInterpolationMode: 'monotone',
          lineTension: 0.1,
          spanGaps: true
        }
      ]
    };

    return data    
  }

  render() {
    let line;
    let data = this.getData();
    if (data != null) {
      line = <Line
      data={data}
      height={150}
      options={{
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            gridLines: {
              drawBorder: true,
              drawOnChartArea: true,
              lineWidth: 0.0,
              zeroLineWidth: 0.5
            },
            ticks: {
              autoSkip: true,
              maxTicksLimit: 20
            }
          }],
          yAxes: [{
            gridLines: {
              drawOnChartArea: true,
              lineWidth: 0.2,
              zeroLineWidth: 0.5
            }
          }]
        }
      }}></Line>
    } else {
      line = <h6 style={{ color:'#333333' }}>데이터 로딩 중...</h6>
    }

    return (
        <Card style={{ width:'94%', marginTop:'15px', marginLeft:'3%' }}>
          <Card.Body>
            <Card.Title>{this.props.title}</Card.Title>
            <Card.Text style={{ color:'#999999' }}>{this.props.text}</Card.Text>
            {/*<>
              <Button size='sm' variant="outline-secondary" style={{ width:'40px', height:'25px', justifyContent:'center'}}>1d</Button>{' '}
              <Button size='sm' variant="outline-secondary" style={{ width:'40px', height:'25px', justifyContent:'center'}}>1m</Button>{' '}
              <Button size='sm' variant="outline-secondary" style={{ width:'40px', height:'25px', justifyContent:'center'}}>6m</Button>{' '}
              <Button size='sm' variant="outline-secondary" style={{ width:'40px', height:'25px', justifyContent:'center'}}>1y</Button>{' '}
              <Button size='sm' variant="outline-secondary" style={{ width:'40px', height:'25px', justifyContent:'center'}}>3y</Button>{' '}
              <Button size='sm' variant="outline-secondary" style={{ width:'40px', height:'25px', justifyContent:'center'}}>5y</Button>{' '}
              <Button size='sm' variant="outline-secondary" style={{ width:'50px', height:'25px', justifyContent:'center'}}>Max</Button>{' '}
            </>
            */}
            <Card.Text></Card.Text>
            { line }
          </Card.Body>
        </Card>
    )
  }
}

export default ContentCard;