import React from 'react';
import { Navbar } from 'react-bootstrap';
//import { render } from '@testing-library/react';

import './App.css';

import ContentCard from './ContentCard';
import LandPriceCard from './LandPriceCard';

import DowJonesIndexCard from './DowJonesIndexCard';
import KospiIndexCard from './KospiIndexCard';

import GoldIndexCard from './GoldIndexCard';
import ExchangeRateCard from './ExchangeRateCard';

let show_land = true;
let is_production = true;

function App() {
  let dow_jones_index_card;
  let kospi_index_card;
  let exchange_rate_card;
  let land_price_card;
  let gold_index_card;

  if (is_production) {
    if (show_land) {
      land_price_card = <LandPriceCard title='서울 부동산 매매가격지수' text='서울 부동산 매매 가격의 등락을 시각화한다.'></LandPriceCard>
    }
  
    dow_jones_index_card = <DowJonesIndexCard title='다우존스 지수' text='다우존스 지수를 시각화한다.'></DowJonesIndexCard>
    kospi_index_card = <KospiIndexCard title='코스피 지수' text='코스피 지수를 시각화한다.'></KospiIndexCard>
    exchange_rate_card = <ExchangeRateCard title='환율' text='원/달러 환율'></ExchangeRateCard>
    gold_index_card = <GoldIndexCard title='금 지수' text='금 지수를 시각화한다.'></GoldIndexCard>
  } else {
    kospi_index_card = <KospiIndexCard title='코스피 지수' text='코스피 지수를 시각화한다.'></KospiIndexCard>
  }

  return (
    <div className="App">
      <header className="App-header">
        <Navbar expand='lg'>
          <Navbar.Brand href="#"><h3>잡동사니</h3></Navbar.Brand>
        </Navbar>
        { land_price_card }
        { kospi_index_card }
        { dow_jones_index_card }
        { exchange_rate_card }
        { gold_index_card }
        <ContentCard title='경제 지표의 상관관계 (준비중)' text='경제 지표를 상관관계지수를 통해 시각화한다.'></ContentCard>
        <ContentCard title='주식과 부동산 (준비중)' text='주식과 부동산 어느 지표가 더 저평가 되어 있나?'></ContentCard>
      </header>
    </div>
  );
}

export default App;
