import React from 'react';

class DataSource extends React.Component {
  static shared = DataSource;

  useProxy = true;
  proxyUrl = 'https://cors-anywhere.herokuapp.com/';

  constructor() {
    super();
    this.state = { data: [] };
  }

  getProxy() {
    if (this.useProxy) {
      return this.proxyUrl;
    }

    return "";
  }

  formatDate(timestamp) {
    var d = new Date(timestamp * 1000),
    month = '' + (d.getMonth() + 1),
    day = '' + d.getDate(),
    year = d.getFullYear();

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return [year, month, day].join('-');
  }

  requestYahooFinance(code) {
    var now = Date.now().toString();

    var targetUrl = 'https://query1.finance.yahoo.com/v8/finance/chart/' + code + '?period1=1546268400&period2=' + now + '&interval=1d';  

    var x = new XMLHttpRequest();
    x.open('GET', this.getProxy() + targetUrl);

    var self = this;
    x.onload = x.onerror = function() {
      if (x.status === 404) return;
      var result = JSON.parse(x.responseText);
      result = result.chart.result[0];
      self.setState({ data: result })
    };
    x.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
    x.send('');
  }

}

export default DataSource;