import './App.css';

import ContentCard from './ContentCard';

class ExchangeRateCard extends ContentCard {
  async componentDidMount() {
    this.requestYahooFinance('KRW=X');
  }

  getValues() {
    var indices = [];
    var values = [];

    var result = this.state.data;

    if (result.length === 0) return [indices, values];

    for (var index in result.timestamp) {
      indices.push(this.formatDate(result.timestamp[index]));
    }

    var adjclose = result.indicators.adjclose[0].adjclose;
    for (index in adjclose) {
      values.push(parseInt(adjclose[index]));
    }
    return [indices,values];
  }
}

export default ExchangeRateCard;